:root {
    --primary: #22D2A0;
    --secondary: #192824;
    --background: #192824;
    --green: #1FC11B;
    --yellow: #FFD913;
    --orange: #FF9C55;
    --red: #FF5555;
    --led-red:#ff007d;
    --led-green:#38FF12;
    --led-blue:#1700ff;
    --led-yellow:#FFF100;
    --led-ligth-blue:#87F9FC;
    --led-purpule:#CC83FF;
    
    --primary-font: 'Kanit', sans-serif;
    --logo-font: 'Audiowide', cursive;
  } 

.my-nav{
    z-index: 2;
    /* height: 100vh; */
    padding: 0 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    width: 100%;
    background: #001437e8;
    border-bottom: 1px solid #22D2A0;
    box-shadow: 0px 0px 50px #87F9FC;
    box-shadow: 0px 0px 50px var(--led-ligth-blue);
}
.nav-menu{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
}
.menu-name{
    padding:1rem 2rem;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    color: darkblue;
}
.menu-link{
    padding:1rem 2rem;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    color: #87F9FC;
    color: var(--led-ligth-blue);
    font-family: 'Audiowide', cursive;
    font-family: var(--logo-font);
    text-shadow: 0px 0px 2px #87F9FC;
