.menu-link:hover{
    color: #22D2A0;
    color: var(--primary);   
  
    text-shadow: 0px 0px 10px  #87F9FC;   
  
    text-shadow: 0px 0px 10px  var(--led-ligth-blue);
    /* border-radius: 10px; */
}
.menu-link:active{
    color: #38FF12;
    color: var(--led-green);   
    text-shadow: 0px 0px 20px  #22D2A0;   
    text-shadow: 0px 0px 20px  var(--primary);
}

.hamburger{
    display: none;
    cursor: pointer;
}

.menu-logo{
    padding: 1rem;
    color:darkblue;
    text-decoration: none;
    font-weight: 800;
    font-size:1.7rem;
    font-family: 'Audiowide', cursive;
    font-family: var(--logo-font);
